import React from 'react';
import { connect } from 'react-redux';
import { Table, Button, Label, Row, Col  } from 'reactstrap';
import { AvForm, AvField, AvGroup } from 'availity-reactstrap-validation';
import { fetchUsers, fetchUsersPage, fetchUsersSearch } from './actions';
import Pagination from "react-js-pagination";
import './UserList.css';
import { Link } from 'react-router-dom';



class UserEditDelete extends React.Component {  
    constructor(props) {
        super(props);
        this.state = {
          activePage: 1,
          email: null
        };
        this.handleSubmit = this.handleSubmit.bind(this); 
        this.handleChange = this.handleChange.bind(this);
      } 

     handleChange(event) {
        const {name, value} = event.currentTarget;        
        if(name === 'userEmail') {
            this.setState({ email: value })
    }}


    handlePageChange(pageNumber) {
        console.log(`active page is ${pageNumber}`);
        this.setState({activePage: pageNumber});
        this.props.fetchUsersPage(pageNumber);        
    }

    handleSubmit(event, errors, values) {
        console.log('entra al submit')
        if(errors.length===0){   
            console.log('entra al metodo de buscar')     
          this.props.fetchUsersSearch(values.userEmail)   
          this.setState({activePage: 1});               
                              
        } else console.log('no entra al metodo')
      };

    componentDidMount(){
        this.props.fetchUsers();
    }

    renderList(){
          return this.props.users.map (user => {              
              if(user.key){             
            return (              
                    <tr key={user.key}>
                    <td>{user.name}</td> 
                    <td>{user.email}</td>                                                            
                    <td><Link to={`/users/edit/${user.key}`} className="button primary"><Button>Editar</Button></Link></td>
                    <td><Link to={`/users/delete/${user.key}`} className="button negative"><Button color="danger">Eliminar</Button></Link></td>
                    </tr> 
                )}      
            }
        )  
        
    };

  
    render() {
        return (
          
            <div>
                <div>
                <Row>
                    <Col sm="12" md={{ size: 7 }}>
                        <h2 className="text-center">Usuarios</h2>
                    </Col>                    
                <Col sm="12" md={{ size: 5 }}>
                    <div className="buscar">
                <AvForm onSubmit={this.handleSubmit}>
                    <AvGroup>
                    <AvField
                        type="text"
                        name="userEmail"
                        placeholder="Email del Usuario"
                        id="userEmail"
                        onChange={this.handleChange}
                        value = {this.state.email}
                        errorMessage="Debe introducir un par치metro de b칰squeda"
                        validate={{
                            required: {value: true}                            
                        }}                  
                    />   
                    </AvGroup>
                    <Button color="danger" type="submit">Buscar Usuario</Button>
                </AvForm> 
                </div>
                </Col>  
                </Row> 
                </div>             
                <Table hover className="text-center">
                    <thead>
                        <tr>
                            <th>Nombre</th>
                            <th>Correo</th>                            
                            <th>Editar</th>
                            <th>Eliminar</th>
                        </tr>
                    </thead>
                    <tbody>{this.renderList()}</tbody>                    
                </Table>   
                <Pagination
                        activePage={this.state.activePage}
                        itemsCountPerPage={10}
                        totalItemsCount={5440}
                        pageRangeDisplayed={8}
                        itemClass="page-item"
                        linkClass="page-link"
                        onChange={this.handlePageChange.bind(this)}         
                    />                             
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    // convierte el objeto users en una matriz
    return {
        users: Object.values(state.users)     
    };
};

export default connect(mapStateToProps, { fetchUsers, fetchUsersPage, fetchUsersSearch })(UserEditDelete);

