export function saveSkooter (error) {
    return{
      type: SAVE_ERROR,
      payload: error
    };
}

export function createSkooter  (formValues) {
    return dispatch => {
        let skooters = {
            id: formValues.skooterIdskooter,
            bateria: '',
            battery_level: '',
            cobertura: '',
            ultima_latitud: '',
            ultima_longitud: ''
          };      
          let setDoc = dbStore.collection('skooters').doc(formValues.skooterIdskooter).set(skooters).catch(error => dispatch(saveSkooter(error)));
        }            
};

export function editSkooter  (idOld, values) {
  return dispatch => {
        let skooterRef = dbStore.collection('skooters').doc(idOld);
        let getDoc = skooterRef.get()
          .then(doc => {
          if (!doc.exists) {
            console.log('No such document!');
         } else {
       let setSkooter = dbStore.collection('skooters').doc(values.skooterIdskooter).set(doc.data())
       let refSkooter = dbStore.collection('skooters').doc(values.skooterIdskooter)       
       let updateId = refSkooter.update({id: values.skooterIdskooter}).then(dbStore.collection('skooters').doc(idOld).delete()) 
       dispatch({ type: DELETE_SKOOTER, payload: idOld });   
      }
  })
  .catch(err => {
    console.log('Error getting document', err);
  });    
      
  }
      };


export const fetchSkooters = () => dispatch => {
  let skootersRef = dbStore.collection('skooters').orderBy('id').limit(10)
let allSkooters = skootersRef.get()
  .then(snapshot => {
    const skooters = []
    snapshot.forEach(skooter => {      
      skooters.push({ key: skooter.id, ...skooter.data() });
    });
    dispatch({ type:FETCH_SKOOTERS, payload: skooters });    
  })
  .catch(err => {
    console.log('Error getting documents', err);
  });  
  
}

