export const fetchUsersSearch = (email) => dispatch => {
  let usersRef = dbStore.collection('users').where('email', '>=', email).where('email', '<=', email+"z")
let allUsers = usersRef.get()
  .then(snapshot => {
    const users = []
    snapshot.forEach(user => {  
      users.push({ key: user.id, ...user.data() });
    });
    dispatch({ type:FETCH_USERS, payload: users });    
  })
  .catch(err => {
    console.log('Error getting documents', err);
  });  
  
}


export const fetchUser = (id) => async dispatch => {
  let responseUser = dbStore.collection('users').doc(id).get().then((doc) => {
    // check if the document exists
    if (doc.exists) {
      const response = doc.data();
      dispatch({ type: FETCH_USER, payload: response });      
    } else {
      throw new Error('No Such Document');
    }
})    
}

export const deleteUser = (id) => async dispatch => {
  let deleteUser = dbStore.collection('users').doc(id).delete()
  dispatch({ type: DELETE_USER, payload: id });    
}