import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUserCircle, faStar, faMapMarked, faSignOutAlt } from '@fortawesome/free-solid-svg-icons';
import SubMenu from './SubMenu';
import { NavItem, NavLink, Nav } from 'reactstrap';
import classNames from 'classnames';
import {Link} from 'react-router-dom';
import logo from '../../assets/img/logo-red.svg'


const SideBar = props => (
    <div className={classNames('sidebar', {'is-open': props.isOpen})}>
      <div className="sidebar-header">
        <span color="info" onClick={props.toggle} style={{color: '#fff'}}>&times;</span>
        <br></br>
        <img class="mx-auto d-block" src={logo} width="200px" height="50px"/> 
        <br></br>                
      </div>
      <div className="side-menu">
        <Nav vertical className="list-unstyled pb-3">
          <p>Panel Administrativo</p>
          
          <SubMenu title="Usuarios" icon={faUserCircle} items={submenus[0]}/>               
          
          <SubMenu title="Skooters" icon={faStar} items={submenus[1]}/>
          
          <SubMenu title="Sitios" icon={faMapMarked} items={submenus[2]}/>
          
          <NavItem>
            <NavLink tag={Link} to={'/salir'}>
              <FontAwesomeIcon icon={faSignOutAlt} className="mr-2"/>Salir
            </NavLink>
          </NavItem>
        </Nav>        
      </div>
    </div>
  );

  const submenus = [
    [
      {
        title: "Agregar Usuario",
        target: "/Add_user"
      },
      {
        title: "Listar Usuarios",
        target: "/List_users",        
      },
      {
        title: "Modificar Eliminar Usuarios",
        target: "/Upd_Delete_users",      
      }
    ],
    [
      {
        title: "Agregar Skooter",
        target: "/Add_skooter"
      },
      {
        title: "Listar Skooter",
        target: "/List_skooters",        
      },
      {
        title: "Modificar Eliminar Skooter",
        target: "/Upd_Delete_skooters",      
      }
    ],
    [
      {
        title: "Agregar Sitios",
        target: "/Add_places"
      },
      {
        title: "Listar Sitios",
        target: "/List_places",        
      },
      {
        title: "Modificar Eliminar Sitios",
        target: "/Upd_Delete_places",      
      }
    ]
  ]
  

export default SideBar;
