import React from 'react';
import { connect } from 'react-redux';
import { fetchUser, editUser } from './actions';
import PropTypes from 'prop-types';
import { Button, FormGroup, Label, Fade } from 'reactstrap';
import { AvForm, AvField, AvGroup } from 'availity-reactstrap-validation';
import { Redirect } from 'react-router-dom';

class UserEdit extends React.Component{
  static propTypes = {
    onSubmit: PropTypes.func.isRequired
    };
    constructor(props) {
      super(props);
      this.state = {
        name: '',
        email: '',
        balance: '',        
        errors: null,
        fadein: false,
        message: ''
    } 
    this.handleSubmit = this.handleSubmit.bind(this);    
};

    componentDidMount() {        
        this.props.fetchUser(this.props.match.params.id);
    };

    handleSubmit(event, errors, values) {
      if(errors.length===0){        
        this.props.editUser(this.props.match.params.id, values)                  
        this.message('Usuario Editado Correctamente');                     
      } else this.message('Error campos no v치lidos');
    };

    message = (message) =>{
      this.setState({              
        fadein: true,
        message: message
      })
      setTimeout (()=>{
        this.setState({
          fadein: false,
          message: ''
        })
      },3000);      
    };

    render() {            
    if (!this.props.user){
      console.log('entro aki') 
        return (<Redirect to={{
          pathname: '/Upd_Delete_users'
        }}/>)
    }
    else {        
        const defaultValues = {
            userName: this.props.user.name,
            userEmail: this.props.user.email,
            userBalance: this.props.user.balance
        }
    return (                 
      <div> 
      <AvForm onSubmit={this.handleSubmit} model={defaultValues}>
    <AvGroup>
      <Label htmlFor="userName" className="block">
        Nombre:
      </Label>
      <AvField
        type="text"
        name="userName"
        value = {this.state.name}
        placeholder="Nombre de usuario"
        id="userName"
        errorMessage="Nombre no v치lido"
        validate={{
            required: {value: true},
            pattern: {value: '^([a-z침치칠칤칩칰A-Z칌츼칄칈칍칔0-9_-]*)+$'},
            minLength: {value: 2},
            maxLength: {value: 20}
          }}
      />
      </AvGroup>   
      <FormGroup>
      <Label htmlFor="userEmail" className="block">
        Correo:
      </Label>
      <AvField
        type="email"
        name="userEmail"
        value = {this.state.email}
        placeholder="Email del Usuario"
        id="userEmail"
        errorMessage="El correo del usuario es obligatorio"
        validate={{
            required: {value: true},
            minLength: {value: 4},
            maxLength: {value: 40}
          }}                  
      />   
      </FormGroup>
      <FormGroup>
      <Label htmlFor="userBalance" className="block">
        Balance:
      </Label>
      <AvField
        type="number"
        name="userBalance"
        value = {this.state.balance}
        placeholder="1"
        id="userBalance"        
      />
      </FormGroup>                  
    <Button color="danger" type="submit">Editar Usuario</Button>
    </AvForm>
    <Fade in={ this.state.fadein } tag="h3" className="mt-3 text-center text-success">
      { this.state.message }
      </Fade>       
      </div>   
    );}
    }
};

const mapStateToProps = (state, ownProps) => {
    return { user: state.users[ownProps.match.params.id]};
};

export default connect(mapStateToProps, { fetchUser, editUser })(UserEdit);