import React from 'react';
import { connect } from 'react-redux';
import { Table, Button, Label, Row, Col  } from 'reactstrap';
import { AvForm, AvField, AvGroup } from 'availity-reactstrap-validation';
import { fetchSkooters, fetchSkootersPage, fetchSkootersSearch } from './actions';
import Pagination from "react-js-pagination";
import './SkooterList.css';
import { Link } from 'react-router-dom';

class SkooterEditDelete extends React.Component {      
    constructor(props) {
        super(props);
        this.state = {
          activePage: 1,
          id: null
        };

        this.handleSubmit = this.handleSubmit.bind(this); 
        this.handleChange = this.handleChange.bind(this);
      }

      handleChange(event) {
        const {name, value} = event.currentTarget;        
        if(name === 'userEmail') {
            this.setState({ email: value })
    }} 

    handlePageChange(pageNumber) {
        console.log(`active page is ${pageNumber}`);
        this.setState({activePage: pageNumber});
        this.props.fetchSkootersPage(pageNumber);        
      }

    handleSubmit(event, errors, values) {
        console.log('entra al submit')
        if(errors.length===0){   
            console.log('entra al metodo de buscar')     
          this.props.fetchSkootersSearch(values.skooterId)   
          this.setState({activePage: 1});               
                              
        } else console.log('no entra al metodo')
      };

   componentDidMount(){
        this.props.fetchSkooters();
    }

    renderList(){
          return this.props.skooters.map (skooter => {
            if(skooter.key){ 
            return (
                    <tr key={skooter.key}>
                    <td>{skooter.id}</td>                                                               
                    <td><Link to={`/skooters/edit/${skooter.id}`} className="button primary"><Button>Editar</Button></Link></td>
                    <td><Link to={`/skooters/delete/${skooter.id}`} className="button negative"><Button color="danger">Eliminar</Button></Link></td>
                    </tr>          
                 )}        
            }
        )
    }

  
    render() {
        return (
            <div>
                <div>
                <Row>
                    <Col sm="12" md={{ size: 7 }}>
                        <h2 className="text-center">Skooters</h2>
                    </Col>                    
                <Col sm="12" md={{ size: 5 }}>
                    <div className="buscar">
                <AvForm onSubmit={this.handleSubmit}>
                    <AvGroup>
                    <AvField
                        type="text"
                        name="skooterId"
                        placeholder="Id Skooter"
                        id="skooterId"
                        onChange={this.handleChange}
                        value = {this.state.id}
                        errorMessage="Debe introducir un par치metro de b칰squeda correcto"
                        validate={{
                            required: {value: true},
                            pattern: {value: '^CX[0-9]+$'},
                            minLength: {value: 2},
                            maxLength: {value: 6}                            
                        }}                  
                    />   
                    </AvGroup>
                    <Button color="danger" type="submit">Buscar Skooter</Button>
                </AvForm> 
                </div>
                </Col>  
                </Row> 
                </div> 
                <Table hover className="text-center">
                    <thead>
                        <tr>
                            <th>Nombre</th>                            
                            <th>Editar</th>
                            <th>Eliminar</th>
                        </tr>
                    </thead>
                    <tbody>{this.renderList()}</tbody>                     
                </Table>  
                <Pagination
                        activePage={this.state.activePage}
                        itemsCountPerPage={10}
                        totalItemsCount={140}
                        pageRangeDisplayed={8}
                        itemClass="page-item"
                        linkClass="page-link"
                        onChange={this.handlePageChange.bind(this)}         
                    />                             
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    // convierte el objeto skooters en una matriz
    return {
        skooters: Object.values(state.skooters)     
    };
};

export default connect(mapStateToProps, { fetchSkooters, fetchSkootersPage, fetchSkootersSearch })(SkooterEditDelete);

