import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { withRouter, Router } from 'react-router-dom';
//import 'bootstrap/dist/css/bootstrap.min.css';
import { authActions, getAuth } from './auth';
import RequireAuthRoute from './components/require-auth-route';
import RequireUnauthRoute from './components/require-unauth-route';
import SignInPage from './pages/sign-in';
import TasksPage from './pages/tasks';
import history from '../src/history';


const App = ({authenticated, signOut}) => (
  <Router history={history}>
     <div>
      <main>  
          <RequireUnauthRoute authenticated={authenticated} const path="/sign-in" component={SignInPage}/>     
          <RequireAuthRoute authenticated={authenticated} const exact path="/" component={TasksPage}/>               
        </main>     
      </div> 
      </Router>        
  );

  App.propTypes = {
    authenticated: PropTypes.bool.isRequired,
    signOut: PropTypes.func.isRequired
  };

//=====================================
//  CONNECT
//-------------------------------------

const mapStateToProps = getAuth;

const mapDispatchToProps = {
  signOut: authActions.signOut
};

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(App)
);

