import React from 'react';
import { connect } from 'react-redux';
import { fetchSkooter, editSkooter } from './actions';
import PropTypes from 'prop-types';
import { Button, Label, Fade } from 'reactstrap';
import { AvForm, AvField, AvGroup } from 'availity-reactstrap-validation';
import { Redirect } from 'react-router-dom';

class SkooterEdit extends React.Component{
  static propTypes = {
    onSubmit: PropTypes.func.isRequired
    };
    constructor(props) {
      super(props);
      this.state = {
        errors: null,
        fadein: false,
        message: ''
    }      
  this.handleSubmit = this.handleSubmit.bind(this); 
}

    componentDidMount() {
        this.props.fetchSkooter(this.props.match.params.id);
    }

    handleSubmit(event, errors, values) {
      if(errors.length===0){
      this.props.editSkooter(this.props.match.params.id, values)
      this.message('Skooter Editado');       
      } else this.message('Error campos no v치lidos');
    }

    message = (message) =>{
      this.setState({      
        fadein: true,
        message: message
      })
      setTimeout (()=>{
        this.setState({
          fadein: false,
          message: ''
        })
      },3000);
    }

    render() {          
    if (!this.props.skooter){                 
        return (<Redirect to={{
          pathname: '/Upd_Delete_skooters'
        }}/>)    
    }
    else {        
        const defaultValues = {
            skooterIdskooter: this.props.skooter.id           
        }
    return (
        <div>
            <h3>Editar un Skooter</h3>
            <div> 
          <AvForm onSubmit={this.handleSubmit} model={defaultValues}>
        <AvGroup>
          <Label htmlFor="skooterIdskooter" className="block">
            Id Skooter:
          </Label>
          <AvField
            type="text"
            name="skooterIdskooter"
            placeholder="CX####"
            id="skooterIdskooter"
            errorMessage="Id no v치lido"
            validate={{
                required: {value: true},
                pattern: {value: '^CX[0-9]+$'},
                minLength: {value: 6},
                maxLength: {value: 6}
              }}
          />
          </AvGroup>          
        <Button color="danger" type="submit">Modificar Skooter</Button>
        </AvForm>  
        <Fade in={ this.state.fadein } tag="h3" className="mt-3 text-center text-success">
          { this.state.message }
          </Fade>                  
          </div>          
        </div>    
    );}
    }
}

const mapStateToProps = (state, ownProps) => {
    return { skooter: state.skooters[ownProps.match.params.id]};
};

export default connect(mapStateToProps, { fetchSkooter, editSkooter })(SkooterEdit);