import React from 'react';
import { connect } from 'react-redux';
import { Table } from 'reactstrap';
import { fetchUsers, fetchUsersPage } from './actions';
import Pagination from "react-js-pagination";
import './UserList.css';

class UserList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
          activePage: 1
        };
      }

    handlePageChange(pageNumber) {
        console.log(`active page is ${pageNumber}`);
        this.setState({activePage: pageNumber});
        this.props.fetchUsersPage(pageNumber);        
      }
         
    componentDidMount(){
        this.props.fetchUsers()        
    }

    renderList(){
        const usersArray = this.props.users
          return (
              usersArray.map ((user) => {
            if(user.key){                 
            return (                                
                <tr key={user.key}>
                <td>{user.name}</td>
                <td>{user.email}</td>
                <td>{user.id}</td>
                <td>{user.balance}</td> 
                <td>{user.stripeCustomerId}</td>                   
                </tr>
                )
            }        
          }) 
           )
    }

  
    render() {
        return (
            <div>
                <h2 className="text-center">Usuarios</h2>
                <Table hover className="text-center">
                    <thead>
                        <tr>
                            <th>Nombre</th>
                            <th>Correo</th>
                            <th>ID</th>
                            <th>Balance</th>
                            <th>Customer ID</th>
                        </tr>
                    </thead>                
                <tbody>{this.renderList()}</tbody> 
                </Table>     
                <Pagination
                        activePage={this.state.activePage}
                        itemsCountPerPage={10}
                        totalItemsCount={5440}
                        pageRangeDisplayed={8}
                        itemClass="page-item"
                        linkClass="page-link"
                        onChange={this.handlePageChange.bind(this)}         
                    />        
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    // convierte el objeto users en una matriz
    return {
        users: Object.values(state.users)            
    };
};

export default connect(mapStateToProps, { fetchUsers, fetchUsersPage })(UserList);

