import React from 'react';
import { connect } from 'react-redux';
import { Button, FormGroup, Label } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEnvelope } from '@fortawesome/free-solid-svg-icons';
import { faGoogle } from '@fortawesome/free-brands-svg-icons';
import { AvForm, AvField } from 'availity-reactstrap-validation';
import { signInWithGoogle, signInWithEmailPassword } from '../../auth/actions';
import './sign-in-page.css';
import logo from '../../assets/img/logo-red.svg';

class SignInPage extends React.Component {
      constructor(props) {
      super(props);
      this.state = {
        email: '',
        password: '',
        error: null
    } 
    this.handleSubmit = this.handleSubmit.bind(this);    
    this.onChangeHandler = this.onChangeHandler.bind(this); 
};

onChangeHandler = (event) => {
    const {name, value} = event.currentTarget;
  
    if(name === 'userEmail') {
      this.setState({email: value})
    }
    else if(name === 'userPassword'){
      this.setState({password: value})
    }
  }; 

  handleSubmit = (event, errors, values) => {
    if(errors.length===0){  
        console.log(values.userEmail)      
        this.props.signInWithEmailPassword(values.userEmail, values.userPassword)          
    }   
  };

   render(){    
      return (
    <div>
      <div className="sign-in">
      <img class="mx-auto d-block" src={logo} width="200px" height="50px"/>
      <br></br>
          {this.error !== null && <div>{this.error}</div>}
        <AvForm onSubmit={this.handleSubmit}>
        <FormGroup>
          <Label htmlFor="userEmail" className="block">
            Email:
          </Label>
          <AvField
            type="email"
            name="userEmail"
            placeholder="ejemplo123@gmail.com"
            id="userEmail"
            onChange = {(event) => this.onChangeHandler(event)}
            errorMessage="Correo no v치lido"
            validate={{
              required: {value: true},              
              minLength: {value: 3},
              maxLength: {value: 50}
            }}
          />
          </FormGroup>
          <FormGroup>
          <Label htmlFor="userPassword" className="block">
            Password:
          </Label>
          <AvField
            type="password"
            name="userPassword"
            placeholder="Contrase침a"
            id="userPassword"
            onChange = {(event) => this.onChangeHandler(event)}
            errorMessage="La contrase침a debe tener al menos 6 caracteres."
            validate={{
              required: {value: true},
              minLength: {value: 6},
              maxLength: {value: 20}
            }}
          />
          </FormGroup>
        <Button color="secondary" type="submit" className="sign-in__button" ><FontAwesomeIcon icon={faEnvelope} className="mr-2"/>Correo y Contrase침a</Button>
        </AvForm>
        <Button color="danger" className="sign-in__button_red" onClick={this.props.signInWithGoogle}><FontAwesomeIcon icon={faGoogle} className="mr-2"/>Entrar con Google</Button>{' '}
        </div>
    </div>
  );
};
}

const mapStateToProps = (state) => {
  return { auth: Object.values(state.auth) };  
};

export default connect(mapStateToProps, { signInWithGoogle, signInWithEmailPassword })(SignInPage);