import React from 'react';
import PropTypes from 'prop-types';
import { reduxForm } from 'redux-form';
import { Button, Label, Fade } from 'reactstrap';
import { AvForm, AvField, AvGroup } from 'availity-reactstrap-validation';

export class CreateSkooter extends React.Component {
    static propTypes = {
      onSubmit: PropTypes.func.isRequired
      };
      constructor(props) {
        super(props);
    this.state = {
        idskooter: '',
        errors: null,
        fadein: false,
        message: ''
    }

    this.handleChange = this.handleChange.bind(this);   
    this.handleSubmit = this.handleSubmit.bind(this); 
  }
  

  handleChange(event) {
const {value} = event.currentTarget;
    this.setState({ idskooter: value })  
}; 

 
    handleSubmit(event, errors, values) {
      if(errors.length===0){
      this.props.onSubmit(values)
        this.message('Agregado');       
      } else this.message('Error campos no v치lidos');
    }
  
  message = (message) =>{
    this.setState({
      idskooter: '',
      fadein: true,
      message: message
    })
    setTimeout (()=>{
      this.setState({
        fadein: false,
        message: ''
      })
    },3000);
  }

render (){   
  return(  
    <div> 
          <AvForm onSubmit={this.handleSubmit}>
        <AvGroup>
          <Label htmlFor="skooterIdskooter" className="block">
            Id Skooter:
          </Label>
          <AvField
            type="text"
            name="skooterIdskooter"
            value = {this.state.idskooter}
            placeholder="CX####"
            id="skooterIdskooter"
            onChange={this.handleChange}
            errorMessage="Id no v치lido"
            validate={{
                required: {value: true},
                pattern: {value: '^CX[0-9]+$'},
                minLength: {value: 6},
                maxLength: {value: 6}
              }}
          />         
    
          </AvGroup>          
        <Button color="danger" type="submit">Agregar Skooter</Button>
        </AvForm>
        <Fade in={ this.state.fadein } tag="h3" className="mt-3 text-center text-success">
          { this.state.message }
          </Fade>       
          </div>
    );
  };      
};    
  
  export default reduxForm({
    form: 'createSkooter'
})(CreateSkooter);

